.navigation {
  background-color: #22A39F;
  top: 0;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  align-items: center;
  padding: 30px;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
  @media (min-width: 1200px) {
    padding: 0px;
    position: static;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transform: translateY(0%);
  }
}

.header--nav-opened .navigation {
  transform: translateY(0%);
  transition: transform 0.2s ease;
}

.navigation-link {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #fff;
  text-decoration: none;
  font-size: 60px;
  line-height: 100%;
  font-weight: 700;
  transition: color 0.2s ease, opacity 0.3s ease;
  opacity: 0;
  &[href]:hover {
    color: #f3efe0;
  }
  @media (min-width: 1200px) {
    opacity: 1;
    margin-left: 20px;
    font-size: 20px;
    &::after {
      position: absolute;
      content: '';
      height: 4px;
      width: 100%;
      background-color: #fff;
      top:100%;
      left: 0;
      opacity: 0;
      transform: translateY(10px);
      transition: opacity 0.3s, transform 0.3s;
    }
    &--current::after{
      opacity: 1;
      transform: translateY(0px);
    }
    &:hover::after,
    &:focus::after {
      opacity: 1;
      transform: translateY(0px);
    }
  }
}

.header--nav-opened .navigation-link {
  opacity: 1;
}

.header--nav-opened .navigation-link:nth-child(1) {
  transition-property: opacity;
  transition-delay: 0.3s;
}

.header--nav-opened .navigation-link:nth-child(2) {
  transition-delay: 0.45s;
  transition-property: opacity;
}


