.portfolio {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.portfolio-title{
  color: #22A39F;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio-list {
  list-style: none;
  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: minmax(auto, 575px) minmax(auto, 575px);
    gap: 40px;
  }
}

.portfolio-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 12px;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.4;
    z-index: 1;
    pointer-events: none;
  }
  &:hover::after {
    opacity: 0;
  }
  @media (min-width: 1200px) {
    margin-bottom: 0;
  }
}

.portfolio-item-link {
  position: absolute;
  z-index: 1;
  align-items: center;
  display: flex;
  height: 100px;
  width: 100px;
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  background-color: #22a39f;
  border-radius: 50%;
  padding: 10px;
  justify-content: center;
  cursor: pointer;
  transform: scale(0);
  transition: transform 0.3s;
}

.portfolio-item-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  transform: scale(1);
  transition: transform 0.3s;
}

.portfolio-item:hover .portfolio-item-image {
  transform: scale(1.1);
}
.portfolio-item:hover .portfolio-item-link {
  transform: scale(1);
}
