.page-body--hidden {
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 100%;
  padding: 0 15px;

  @media (min-width: 1200px) {
    margin: 0 auto;
    width: 1170px;
  }
}

.button {
  color: #fff;
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-decoration: none;
  padding: 10px 30px;
  background-color: #22A39F;
  border-color: #1c8784;
  border-radius: 4px;
  text-align: center;
  width: 300px;
  transition: background-color .5s, opacity .5s;
  &:hover {
    background-color: #1c8784;
  }
  &:active {
    opacity: 30%;
  }
}

