.page-body--hidden {
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 100%;
  padding: 0 15px;
}
@media (min-width: 1200px) {
  .center {
    margin: 0 auto;
    width: 1170px;
  }
}

.button {
  color: #fff;
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-decoration: none;
  padding: 10px 30px;
  background-color: #22A39F;
  border-color: #1c8784;
  border-radius: 4px;
  text-align: center;
  width: 300px;
  transition: background-color 0.5s, opacity 0.5s;
}
.button:hover {
  background-color: #1c8784;
}
.button:active {
  opacity: 30%;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: lighter;
  font-display: swap;
  src: url("../../fonts/opensanslight.woff2") format("woff2"), url("../../fonts/opensanslight.woff") format("woff");
}
.page {
  height: 100%;
}

.page-body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background-color: #F3EFE0;
  color: #434242;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1.64;
  font-weight: 400;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
}

.header {
  position: relative;
  width: 100%;
  z-index: 2;
  background-color: #22A39F;
  color: #fff;
  line-height: 1.2;
  padding: 30px 80px 30px 30px;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .header {
    min-height: 150px;
    padding: 30px;
  }
}

.header-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
@media (min-width: 1200px) {
  .header-wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header-logo {
  display: flex;
  flex-direction: column;
}

.header-logo p {
  font-size: 26px;
  font-weight: 300;
  color: #fff;
  margin: 0;
  padding: 0;
}

.header-logo h1 {
  margin: 0;
  padding: 0;
}

.header-toggle {
  position: absolute;
  background-color: transparent;
  border: 0;
  right: 30px;
  top: 30px;
  padding: 20px 0;
}
@media (min-width: 1200px) {
  .header-toggle {
    display: none;
  }
}

.header-toggle span {
  display: block;
  width: 40px;
  height: 3px;
  position: relative;
}

.header--nav-closed .header-toggle span {
  background-color: #fff;
}
.header--nav-closed .header-toggle span::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: translateY(-10px);
  transition: transform 0.4s;
}
.header--nav-closed .header-toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: translateY(10px);
  transition: transform 0.4s;
}

.header--nav-opened .header-toggle span {
  background-color: transparent;
}
.header--nav-opened .header-toggle span::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fff;
  left: 0;
  transform: rotate(45deg);
  transition: transform 0.4s;
}
.header--nav-opened .header-toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transform: rotate(-45deg);
  transition: transform 0.4s;
}

.about-me {
  display: flex;
  flex-direction: column;
  gap: 80px;
  padding-top: 60px;
  padding-bottom: 60px;
}

.about-me-link {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
  color: #727878;
}
.about-me-link:hover {
  text-decoration: underline;
}

.about-me-content {
  display: flex;
  flex-direction: column;
  gap: 60px;
}
@media (min-width: 1200px) {
  .about-me-content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.about-me-image {
  align-self: center;
  width: 389px;
  height: 389px;
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
}
@media (max-width: 659px) {
  .about-me-image {
    display: none;
  }
}

.about-me-text {
  line-height: 1.64;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .about-me-text {
    width: 700px;
  }
}

.about-me-text p {
  margin-bottom: 20px;
}

.about-me-title {
  color: #22A39F;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.portfolio-title {
  color: #22A39F;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio-list {
  list-style: none;
}
@media (min-width: 1200px) {
  .portfolio-list {
    display: grid;
    grid-template-columns: minmax(auto, 575px) minmax(auto, 575px);
    gap: 40px;
  }
}

.portfolio-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 12px;
}
.portfolio-item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.4;
  z-index: 1;
  pointer-events: none;
}
.portfolio-item:hover::after {
  opacity: 0;
}
@media (min-width: 1200px) {
  .portfolio-item {
    margin-bottom: 0;
  }
}

.portfolio-item-link {
  position: absolute;
  z-index: 1;
  align-items: center;
  display: flex;
  height: 100px;
  width: 100px;
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  background-color: #22a39f;
  border-radius: 50%;
  padding: 10px;
  justify-content: center;
  cursor: pointer;
  transform: scale(0);
  transition: transform 0.3s;
}

.portfolio-item-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  transform: scale(1);
  transition: transform 0.3s;
}

.portfolio-item:hover .portfolio-item-image {
  transform: scale(1.1);
}

.portfolio-item:hover .portfolio-item-link {
  transform: scale(1);
}

.navigation {
  background-color: #22A39F;
  top: 0;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  align-items: center;
  padding: 30px;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
}
@media (min-width: 1200px) {
  .navigation {
    padding: 0px;
    position: static;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transform: translateY(0%);
  }
}

.header--nav-opened .navigation {
  transform: translateY(0%);
  transition: transform 0.2s ease;
}

.navigation-link {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #fff;
  text-decoration: none;
  font-size: 60px;
  line-height: 100%;
  font-weight: 700;
  transition: color 0.2s ease, opacity 0.3s ease;
  opacity: 0;
}
.navigation-link[href]:hover {
  color: #f3efe0;
}
@media (min-width: 1200px) {
  .navigation-link {
    opacity: 1;
    margin-left: 20px;
    font-size: 20px;
  }
  .navigation-link::after {
    position: absolute;
    content: "";
    height: 4px;
    width: 100%;
    background-color: #fff;
    top: 100%;
    left: 0;
    opacity: 0;
    transform: translateY(10px);
    transition: opacity 0.3s, transform 0.3s;
  }
  .navigation-link--current::after {
    opacity: 1;
    transform: translateY(0px);
  }
  .navigation-link:hover::after, .navigation-link:focus::after {
    opacity: 1;
    transform: translateY(0px);
  }
}

.header--nav-opened .navigation-link {
  opacity: 1;
}

.header--nav-opened .navigation-link:nth-child(1) {
  transition-property: opacity;
  transition-delay: 0.3s;
}

.header--nav-opened .navigation-link:nth-child(2) {
  transition-delay: 0.45s;
  transition-property: opacity;
}

.footer {
  background-color: #333;
  text-align: center;
  color: #a4a4a4;
  font-size: 20px;
  font-weight: 400;
}

.slider {
  position: relative;
  height: 400px;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .slider {
    max-width: 700px;
  }
}

.slider-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  transform: translateX(100%);
  transition: transform 0.5s ease;
}
.slider-item--current {
  transform: translateX(0%);
  transition: transform 0.5s ease;
}

.slider-btn {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 5px 10px;
}
.slider-btn--prev {
  left: 0;
}
.slider-btn--next {
  right: 0;
}
.slider-btn:disabled {
  opacity: 0.5;
}
.slider-btn--next img {
  transform: rotate(180deg);
}

.slider-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}