.slider {
  position: relative;
  height: 400px;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  @media(min-width: 1200px) {
    max-width: 700px;
  }
}

.slider-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  transform: translateX(100%);
  transition: transform 0.5s ease;
  &--current {
    transform: translateX(0%);
    transition: transform 0.5s ease;
  }
}

.slider-btn {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 5px 10px;
  &--prev {
    left: 0;
  }
  &--next {
    right: 0;
  }
  &:disabled {
    opacity: 0.5;
  }
  &--next img {
    transform: rotate(180deg);
  }


}

.slider-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}
